@import url(https://fonts.googleapis.com/css2?family=Spectral:wght@300&display=swap);
*{
    font-family: 'Spectral', serif;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}

.App{
    width: 100vw;
    height: 100vh;
    margin: 0;
    padding: 0%;
}


.h2-Style{
    font-size: 20px;
}

p{
    font-size: 14px;
}

h3{
    font-size: 18px;
}

.pageSection{
    width: 100%;
}

@media (min-width:768px){
    .h2-Style{
        font-size: 24px;
    }

    p{
        font-size: 16px;
    }
    
    h3{
        font-size: 20px;
    }
}

@media (min-width:1024px){
    .h2-Style{
        font-size: 28px;
    }

    p{
        font-size: 18px;
    }
    
    h3{
        font-size: 22px;
    }
}

@media (min-width:1440px){
    .h2-Style{
        font-size: 32px;
    }

    p{
        font-size: 20px;
    }
    
    h3{
        font-size: 24px;
    }
}

@media (min-width:1920px){
    .pageSection{
        margin: 0 auto;
        width: 1440px;
    }
}
.navbar{
    top: 0;
    width: 100%;
    height: 100px;
    background-color: #fff;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    z-index: 10;
}

.logo--navbar{
    display: flex;
    height: 98px;
    

}

.logo--navbar img{
    height: 100%;
    
}
.links-navBar{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
}

.links-navBar span{
    margin-right: 18px;
    font-weight: bold;
    cursor: pointer;
    color: #293591;
    display: none;
}

.navBox{
    width: 70px;
    height: 60px;
    background-color: #293591;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;

    justify-content: center;
    align-items: center;
}


@media (min-width:1024px){

    .navBox svg{
        display: none;
    }

    .links-navBar span{

        display: flex;
    }
}
.slider-container{
    position: relative;
    margin-top: 100px;
    width: 100%;
    height: 200px;
    
}

.slider-img{
    position: absolute;

    width: 100%;
    height: 100%;
    overflow: hidden;
    transition: all 1s ease-in-out;
}

.slider-img img{
    width: 100%;
    height: 100%;
}

.sliderSelector{
    position: absolute;
    bottom: 40px;
    left: 0%;
    width: 100%;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
}


.sliderItem{
    font-size: 16px;
    color: #fff;
    margin: 0 12px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.sliderItem > div{

    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #0e4f9d;
    cursor: pointer;
}

.text-slider{
    
    position: absolute;
    font-weight: bold;
    color: #0e4f9d;
    font-size: 18px;
    transition: opacity 1s ease-in-out;
}

@media (min-width:768px){
    .slider-container{
        height: 300px;
        
    }

    .text-slider{
        font-size: 22px;

    }
}

@media (min-width:1024px){
    .slider-container{
        height: 400px;
        
    }

    .text-slider{
        font-size: 30px;
    }
}

@media (min-width:1440px){
    .slider-container{
        height: 550px;
        
    }

    .text-slider{
        font-size: 38px;
    }
}

@media (min-width:1920px){
    .slider-container{
        height: 600px;
        
    }

    .text-slider{
        font-size: 38px;
    }
}


.empresa-component{
    margin-top: 30px;
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.empresa-header{
    
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}



.empresa-headerText h2 {
    
    color: #fff;
    text-align: center;
    background-color: #293591;
    display: inline-block;
    box-sizing: border-box;
    padding: 10px 15px;
}




.empresa-headerSecondText{
    box-sizing: border-box;
    font-weight: bold;
    padding: 0 20px;
    text-align: right;
    font-size: 20px;
    align-self: flex-end;
    width: 70%;
    color: #293591;
}

.empresa-contenido{
    margin-top: 20px;
    width: 100%;
    box-sizing: border-box;
    padding: 0 20px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    color: #293591;
}

.empresa-contenidoImagenes{
    width: 30%;
}

.empresa-texto{
    width: 65%;
}



.empresa-subSecond{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.empresa-subSecond h3{
    
    font-weight: bold;
}

.empresa-subSecond p{
    font-weight: bold;
    
    margin-left: 15%;
    

}



.empresa-contenidoImagenes img{
    width: 100%;
}

.empresa-contenidoSpecial{
    margin-top: 20px;
    background-color: #293591;
    color: #fff;
    font-size: 26px;
    text-align: center;
}


.empresa-componentContenido{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 100%;
    box-sizing: border-box;
    padding: 0 20px;
    margin-top: 20px;

    color: #293591;
}

.empresa-contenidoItem{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-bottom: 20px;
    justify-content: space-between;

}

.empresa-sub{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-bottom: 20px;
}

.empresa-sub h3{
    
    font-weight: bold;
}

.empresa-sub p{
    font-weight: bold;
    
    margin-left: 20px;
    width: 100%;
}

.empresa-contenidoItem img{
    width: 50%;
    align-self: center;
    
}

.empresa-ciText{
    width: 100%;
}

@media (min-width:768px){

    .empresa-contenidoItem img{
        width: 30%;
    }
    
    .empresa-ciText{
        width: 65%;
    }
    .empresa-contenidoItem{

        flex-direction: row;
        flex-wrap: nowrap;
    }

    .empresa-sub{
        flex-direction: row;
        flex-wrap: nowrap;
    }

    .empresa-sub p{
        width: 80%;
    }

    .empresa-ciText{
        align-self: center;
    }
}





.servicios--component{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 100%;

    box-sizing: border-box;
    padding: 0 20px;
    margin-top: 20px;
    

}

.servicio-item{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
}

.servicio-itemImg{
    width: 50%;
}
.servicio-itemImg img{
    width: 100%;
}

.servicio-itemText{
    width: 100%;

}

.servicio-itemHeader{
    background-color: #293591;
    color: #fff;
    margin-bottom: 20px;
    display: inline-block;
    box-sizing: border-box;
    padding: 10px 15px;
}

.servicio-itemText p{
    color: #293591;
    font-weight: bold;
    font-size: 18px;
    margin-left: 20px;

    width: 100%;
    margin-left: 0;
}

.responsiveHeader-Servicio{
    align-self: flex-start;
}

.responsiveHeader-Servicionone{
    
    display: none;
}

@media (min-width:768px){
    .responsiveHeader-Servicio{
        display: none;
    }

    .responsiveHeader-Servicionone{
    
        display: inline-block;
    }

    .servicio-itemText p{
        color: #293591;
        font-weight: bold;
        font-size: 18px;
        margin-left: 20px;
    
        width: 90%;
        margin-left: 30px;
        margin-bottom: 10px;
    }
    .servicio-itemText{
        width: 65%;
    
    }
    .servicio-itemImg{
        width: 30%;
    }
    .servicio-item{
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-items: center;
    }
}
.contacto-contenedor{
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 100%;
    box-sizing: border-box;
    padding: 0 30px 30px 30px;
}

.contacto-header{
    margin-bottom: 20px;
}

.contacto-titles{
    color: #293591;
    font-size: 20px;
}

.contacto-subContainer{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    
}

.contacto-item{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-bottom: 10px;
}

.contacto-item > span{
    color: #293591;
    font-weight: bold;
    font-size: 16px;
}

.contactoItemHeader{
    margin-bottom: 10px;
}


@media (min-width:768px){
    .contacto-titles{
        color: #293591;
        font-size: 22px;
    }

    .contacto-item > span{
        color: #293591;
        font-weight: bold;
        font-size: 20px;
    }

    .contacto-subContainer{
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
}
